.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App #intro{
  padding-top: 3%;
  font-size: 45px;
}

.summary p {
  font-size: 30px;
}

.summary button {
  width: 180px;
  height: 130px;
  padding: 20px;
}

#creditCardSelect button{
  width: 100px;
  height: 60px;
}

.cardNumbers {
  width: 5%;
  margin-right: 0.5%;
}

.paymentMethod {
  border-width: medium;
  border-style: solid;
  border-color:gainsboro;
}